body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  color: #e0e0e0;
  --s: 100px; /* control the size */
  --c1: #cfcfcf;
  --c2: #1a1a1a;

  --_s: calc(2*var(--s)) calc(2*var(--s));
  --_g: var(--_s) conic-gradient(at 40% 40%, #0000 75%, var(--c1) 0);
  --_p: var(--_s) conic-gradient(at 20% 20%, #0000 75%, var(--c2) 0);
  background:
    calc(0.9*var(--s)) calc(0.9*var(--s)) / var(--_p),
    calc(-0.1*var(--s)) calc(-0.1*var(--s)) / var(--_p),
    calc(0.7*var(--s)) calc(0.7*var(--s)) / var(--_g),
    calc(-0.3*var(--s)) calc(-0.3*var(--s)) / var(--_g),
    conic-gradient(from 90deg at 20% 20%, var(--c2) 25%, var(--c1) 0) 0 0 / var(--s) var(--s);
  animation: m 8s infinite linear;
  background-attachment: fixed;
  background-repeat: repeat;
  overflow: hidden;
}

@keyframes m {
  0% {
    background-position:
      calc(0.9*var(--s)) calc(0.9*var(--s)),
      calc(-0.1*var(--s)) calc(-0.1*var(--s)),
      calc(0.7*var(--s)) calc(0.7*var(--s)),
      calc(-0.3*var(--s)) calc(-0.3*var(--s)), 0 0;
  }
  25% {
    background-position:
      calc(1.9*var(--s)) calc(0.9*var(--s)),
      calc(-1.1*var(--s)) calc(-0.1*var(--s)),
      calc(1.7*var(--s)) calc(0.7*var(--s)),
      calc(-1.3*var(--s)) calc(-0.3*var(--s)), 0 0;
  }
  50% {
    background-position:
      calc(1.9*var(--s)) calc(-0.1*var(--s)),
      calc(-1.1*var(--s)) calc(0.9*var(--s)),
      calc(1.7*var(--s)) calc(-0.3*var(--s)),
      calc(-1.3*var(--s)) calc(0.7*var(--s)), 0 0;
  }
  75% {
    background-position:
      calc(2.9*var(--s)) calc(-0.1*var(--s)),
      calc(-2.1*var(--s)) calc(0.9*var(--s)),
      calc(2.7*var(--s)) calc(-0.3*var(--s)),
      calc(-2.3*var(--s)) calc(0.7*var(--s)), 0 0;
  }
  100% {
    background-position:
      calc(2.9*var(--s)) calc(-1.1*var(--s)),
      calc(-2.1*var(--s)) calc(1.9*var(--s)),
      calc(2.7*var(--s)) calc(-1.3*var(--s)),
      calc(-2.3*var(--s)) calc(1.7*var(--s)), 0 0;
  }
}

body::after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  backdrop-filter: blur(5px) brightness(1.15) saturate(1.2);
  background: linear-gradient(
    rgba(14, 14, 14, 0.979),
    rgb(22, 22, 22)
  );
  mix-blend-mode: screen;
  z-index: -1;
}


.container {
  position: relative;
  height: 100vh;
  overflow: hidden;
}

/* Контейнер для кнопок меню — овальное меню внизу */
.menu-button-container {
  position: fixed;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
  background-color: rgba(30, 30, 30, 0.6);
  padding: 15px 30px;
  border-radius: 26px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(8px);
  z-index: 1000;
  box-shadow: 0 0 2px rgba(255, 255, 255, 0.6);
}

/* Стиль для кнопок */
.menu-button {
  background-color: transparent;
  color: #e0e0e0;
  border: none;
  font-size: 0.9rem;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 6px;
  min-width: 60px;
  text-align: center;
  transition: color 0.3s ease;
  letter-spacing: 1px;
}

/* Стиль для иконок */
.menu-button .menu-icon {
  font-size: 26px;
  color: #cccccc;
  line-height: 1;
}

/* Зеленый цвет иконки у активной кнопки */
.menu-button.active .menu-icon {
  color: #6FCF00;
}

/* Зеленый цвет текста у активной кнопки */
.menu-button.active {
  color: #6FCF00;
}
